html {
    overflow-x: hidden;
    overflow-y: scroll;
    font-size: 62.5%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
    min-height: 100vh;
    margin: 0;
    padding: 0;
    color: #273033;
    font-family: var(--font-sans-serif);
    font-size: 1.8rem;
    line-height: 1.7;
    font-weight: 400;
    font-style: normal;
    background: #fff;
    scroll-behavior: smooth;
    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

label {
    display: block;
    font-size: 0.9em;
    font-weight: 700;
}

hr {
    position: relative;
    display: block;
    width: 100%;
    height: 1px;
    border: 0;
    border-top: 1px solid var(--color-border);
    /* opacity: 0.2; */
}

::selection {
    text-shadow: none;
    background: #cbeafb;
}

mark {
    background-color: #fdffb6;
}

small {
    font-size: 80%;
}

sub,
sup {
    position: relative;
    font-size: 75%;
    line-height: 0;
    vertical-align: baseline;
}
sup {
    top: -0.5em;
}
sub {
    bottom: -0.25em;
}

ul:not([class]) li + li {
    margin-top: 0.2em;
}

a {
    cursor: pointer;
}

a:not([class]) {
    color: var(--ghost-accent-color);
    text-decoration-skip-ink: auto;
}

a[class] {
    outline: none;
    color: inherit;
    text-decoration: none;
    transition: 0.4s ease;
}

a[class]:hover {
    transition: 0.2s ease;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    line-height: 1.15em;
    font-weight: 700;
}

h1 {
    font-size: 7rem;
    font-size: clamp(27px, 4.1vw, 50px);
}
@media (max-width: 500px) {
    h1 {
        font-size: 2.7rem;
    }
}

h2 {
    font-size: 3rem;
}
@media (max-width: 500px) {
    h2 {
        font-size: 2.2rem;
    }
}

h3 {
    font-size: 2.8rem;
}
@media (max-width: 500px) {
    h3 {
        font-size: 1.8rem;
    }
}

h4 {
    font-size: 2.6rem;
}

h5 {
    font-size: 2.4rem;
}

h6 {
    font-size: 2.2rem;
}

strong,
b {
    font-weight: 600;
}

.small-caps {
    text-transform: uppercase;
    font-size: 1.3rem;
    font-weight: 500;
}